.popup {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    opacity:0;
    visibility: hidden;
    transition: visibility 0s linear 0.5s,opacity 0.5s linear;
}